import profile from './../textures/profile.gif';
import { IconArchive, IconBrandGithub, IconBrandX, IconBrandYoutube } from '@tabler/icons-react';
import './ProfileCard.css';
import { motion } from 'framer-motion';
import homeStrings from '../../string';
import { Link } from 'react-router-dom';
import paths from '../../links';
import LinkIcon from './LinkIcon';

const LinkIconStyle = {
    margin: 8,
    marginRight: 0,
    width: 32,
    height: 32,
};

const ProfileCard = (props) => {
    const isMini = props.type === 'mini';

    return (
        <motion.div
            className={isMini ? 'MiniProfileCard' : 'ProfileCard'}
            initial={{
                opacity: 0.0,
                marginTop: -100,
            }}
            animate={{
                opacity: 1.0,
                marginTop: 0,
            }}
            transition={{
                duration: 0.5,
                ease: 'easeOut',
            }}
        >
            <img src={profile} className={isMini ? 'MiniProfileCardImage' : 'ProfileCardImage'} />
            <div className={isMini ? 'MiniProfileCardTitle' : 'ProfileCardTitle'}>
                <h1>{homeStrings.name}</h1>
                <h2>{homeStrings.furigana}</h2>
            </div>
            <div className={isMini ? 'MiniProfileCardDescription' : 'ProfileCardDescription'}>
                <p>{homeStrings.description}</p>
            </div>
            <div className="ProfileLinkIconList">
                <Link to={paths.work} style={{ textDecoration: 'none' }}>
                    <LinkIcon
                        Icon={<IconArchive style={LinkIconStyle} />}
                        isMini={isMini}
                        label={homeStrings.links.works.label}
                        tips={'様々なジャンルの作品をご覧いただけます'}
                    />
                </Link>
                {/* <LinkIcon
            Icon={<IconMail style={LinkIconStyle} />}
            isMini={isMini}
            label="MAIL"
        /> */}
                <LinkIcon
                    Icon={<IconBrandX style={LinkIconStyle} />}
                    isMini={isMini}
                    label={homeStrings.links.twitter.label}
                    bgHoverColor="#aaa"
                    ttHoverColor="#111"
                    onClick={() => window.open(homeStrings.links.twitter.url, '_blank')}
                />
                <LinkIcon
                    Icon={<IconBrandGithub style={LinkIconStyle} />}
                    isMini={isMini}
                    label={homeStrings.links.github.label}
                    bgHoverColor="#000"
                    bdHoverColor="#fff"
                    ttHoverColor="#fff"
                    onClick={() => window.open(homeStrings.links.github.url, '_blank')}
                />
                {/* TODO: Youtube 動画投稿するまで隠す */}
                <LinkIcon
                    Icon={<IconBrandYoutube style={LinkIconStyle} />}
                    isMini={isMini}
                    label={homeStrings.links.youtube.label}
                    bgHoverColor="#ff0000"
                    bdHoverColor="#fff"
                    ttHoverColor="#fff"
                    onClick={() => window.open(homeStrings.links.youtube.url, '_blank')}
                />
            </div>
        </motion.div>
    );
};

export default ProfileCard;
